
import { defineComponent } from 'vue'
import { LocationsMap, LocationData } from '@/assets/LocationsMap'
import { HighlightData } from '@/assets/HighlightSystem'

export default defineComponent({
  name: 'StatsDisplay',
  props: {
    highlightSet: {},
    locMap: {}
  },
  methods: {
    locations () {
      const loc = this.locMap as LocationsMap
      return loc
    },
    highlightList () {
      const hs = this.highlightSet as HighlightData
      if (hs.stateList !== undefined) {
        return hs.stateList()
      }
      return []
    },
    toggle (item: LocationData) {
      const hs = this.highlightSet as HighlightData
      hs.toggleLoc(item, this.notify)
    },
    notify () {
      this.$forceUpdate()
    },
    async mounted () {
      const hs = this.highlightSet as HighlightData
      hs.addSubscriber(this.notify)
    }
  }

})
